.App {
  display: flex;
  justify-content: center;
  max-width: 500px;
  text-align: center;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.product {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.top {
  margin-top: 16px;
}

.productName {
  margin: 0 auto;
  color: #4CAF50;
  font-weight: bold;
  font-size: 110%;
}

.productImg {
  display: block;
  width: 30%;
  margin: 0 auto;
  margin-top: 10px;
  border: 3px solid #339036;
  border-radius: 10px;
}

.price {
  margin-top: 10px;
  color: #4CAF50;
}

.add {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 10px 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  max-width: 133px;
  margin: 0 auto;
}

.count {
  display: flex;
  height: 36.97px;
  margin: 0 auto;
}

.countNumber {
  margin: 3px 10px 0px 10px; 
}

.minus {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 5px 12px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
}

.plus {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 3px 9px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
}

.add:active {
  background-color:#1c791d;
}

.minus:active {
  background-color:#1c791d;
}

.plus:active {
  background-color:#1c791d;
}